import { getStaffModel, StaffService } from '@miq/staffjs';
import { TPriceData } from '@shopy/storejs';

import { TOrderItem } from '../cart';
import { TCustomer } from '../customer';

const timeout = 5000;

export type TOrder = {
  slug?: string;
  transaction_id?: string;
  customer?: string;
  customer_name?: string;
  customer_data?: TCustomer;
  //   notes?: string;
  is_delivered?: boolean;
  dt_delivered?: string;

  items?: TOrderItem[];
  items_count?: number;
  //   products?: string[];

  //   is_placed?: boolean;
  subtotal?: TPriceData;
  total?: TPriceData;

  created?: string;
  updated?: string;
};

class OrderService extends StaffService<TOrder> {
  mark_delivered() {
    if (this.data?.is_delivered) return Promise.resolve({ data: this.data });
    return this.post(`${this._path}deliver/`, {}, { timeout });
  }

  get deliveredDt() {
    return this.formatDt(this.data?.dt_delivered);
  }
  get updatedDt() {
    return this.formatDt(this.data?.updated);
  }
  get createdDt() {
    return this.formatDt(this.data?.created);
  }

  formatDt(dt?: string) {
    if (!dt) return null;
    return new Date(dt).toLocaleDateString(undefined, {
      day: '2-digit',
      weekday: 'short',
      month: 'short',
      year: '2-digit',
    });
  }
}

export const Order = getStaffModel<TOrder, OrderService>('orders', OrderService);

export type TOrderInstance = ReturnType<typeof Order>;
