import * as React from 'react';

// import './views.list.scss';
//
import Staff from '@miq/staffjs';
import { ProductPriceDisplay } from '@shopy/storejs';
import { Card, Icons, Pagination } from '@miq/componentjs';
import { useDocumentTitle } from '@miq/hookjs';

import { Order, TOrder } from './utils';
import { SharedDataCtx, TSDState } from '@miq/contextjs';
import { OrderUpdateLink } from './buttons';

export default function OrderListView() {
  useDocumentTitle('Orders - Sales');

  const { orders, pagination } = React.useContext<TSDState<{ orders: TOrder[]; pagination: any }>>(SharedDataCtx);

  return (
    <Staff.View
      title="Commandes"
      back="/staff/sales/"
      footer={
        pagination && (
          <div>
            <Pagination {...pagination} component={Staff.Link} to />
          </div>
        )
      }
      className="sales-orders-view miq-container"
      headerCN="miq-container"
    >
      {orders?.map((i) => {
        const order = Order(i);

        return (
          <Card
            // border
            style={{ marginBottom: 12 }}
            actions={
              <div className="d-flex">
                <OrderUpdateLink instance={order} className="btn btn-round btn-primary-3 ms-1">
                  <Icons.Edit />
                </OrderUpdateLink>
              </div>
            }
            title={<div className="">{`${order.customer_name}`}</div>}
            key={i.slug}
            className="cart-list-item-card mb-1"
          >
            <ul className="text-sm">
              <li className="d-flex align-items-center justify-content-between">
                <span className="">{order.items_count} produit(s)</span>

                <span className="fw-bold">
                  <ProductPriceDisplay retail_price_data={i?.total || i?.subtotal} />
                </span>
              </li>
              <li className="text-sm text-muted">{order.createdDt}</li>
            </ul>
          </Card>
        );
      })}
    </Staff.View>
  );
}
