import * as React from 'react';
import { LinkProps } from 'react-router-dom';

import Staff from '@miq/staffjs';

import { TOrder, TOrderInstance } from './utils';
import { Button, ConfirmButton, Icons } from '@miq/componentjs';

export const OrderMarkDelivredButton = ({
  instance,
  ...props
}: {
  instance: TOrderInstance;
  icon?: boolean;
  onSuccess: (res?: any) => void;
  onFailure?: (res?: any) => void;
}) => {
  return (
    <ConfirmButton
      renderHeader={() => <div className="text-md p-1">Le client a t'il re√ßu le produit?</div>}
      renderFooter={({ setOpen }) => (
        <div className="d-flex justify-content-between align-items-center">
          <Button onClick={() => setOpen(false)}>Pas encore</Button>

          <Button
            onClick={() =>
              instance
                .mark_delivered()
                .then((r) => {
                  props?.onSuccess?.(r);
                  // setOpen(false);
                })
                .catch((err) => {
                  props?.onFailure?.(err);
                })
            }
            className="btn-primary fw-bold"
          >
            Oui
          </Button>
        </div>
      )}
      render={() => <div className="p-3"></div>}
      className="btn-primary btn-md fw-bold w-50 d-flex align-items-center justify-content-center"
    >
      {instance?.is_delivered ? <Icons.Truck /> : <Icons.TruckOff />}
      {!props?.icon && <span className="ms-1">Confirmer la livraison</span>}
    </ConfirmButton>
  );
};

export const OrderUpdateLink = (props: Omit<LinkProps, 'to'> & { instance: TOrder | TOrderInstance }) => (
  <Staff.Link
    title={`Modifier cette commande`}
    {...props}
    to={`/staff/sales/orders/${props.instance.slug}/`}
    requiredPerms={['sales.change_order']}
  >
    {props.children}
  </Staff.Link>
);
